import { Rule } from 'type/rule'
import { Context } from 'type/context'
import { RuleUpdate, UpdateContext } from 'type/update'
import { update } from '..'
import { tap } from 'utils'
import { findTargetElements } from './element'
import { ruleToUpdates } from './update'
import { registerEvent } from './web'

export const isWindow = (t: string) => { return t === 'window'; }

export const isWindowRule = (rule: Rule) => isWindow(rule.input.target)

export const loadRule: Rule = {
  input: { event: 'load', target: 'window' },
  output: { action: 'initData', target: '' }
}

export const syncRule: Rule = {
  input: { event: 'click', target: '__sync' },
  output: { action: 'sync', target: '' }
}

export const createInputRule = (target: string): Rule => ({
  input: { event: 'change', target },
  output: { action: 'input', target }
})

export const registerRule = (context: Context, ru: RuleUpdate) =>
  findTargetElements(ru.input.target, ru.parent).forEach(
    registerEvent(
      ru.input.event,
      e => update(context, ruleToUpdates({ context, ...ru.output, eventData: e }))
    )
  )
