import { isNil } from 'ramda';
import { Update, DataUpdate, RuleUpdate, EffectUpdate, Effect, DataContext, RuleContext, UpdateContext } from 'type/update';
import { Schema, Field, getField, ObjectField } from 'type/schema';
import { Data, Context } from 'type/context';
import { NestedList } from 'type/lang';
import { Rule, Output, Value } from 'type/rule';
import { UIElement } from 'type/ui';
import { ListOp, UpdateType } from 'type/enum';
import { isDataUpdate, isObjectField, isListField, isOptionField, isArray } from 'type/guard';
import { cases, when, flatten, use, tap, fmap, api, post } from 'utils';
import { setAll, get, pre, filterList, sortList } from 'utils/path';
import { evalBinaryExp } from 'utils/expression'
import { nextUID } from 'utils/ui';
import { isWindowRule, createInputRule } from './rule';
import { findAttr, registerEvent, pathToElements } from 'ui/web';
import { update } from '..'

const evalUpdateValue = when<DataContext, Value>(
  c => c.value || c.calcValue(c.data),
  [c => c.listOp == ListOp.clear, c => []]
)

const evalUpdatePath = cases<DataContext, string>(
  'listOp', c => c.path,
  [ListOp.add, c => c.path + '.' + (get(c.path, c.data) || []).length]
)

export const evalUpdate = (c: DataContext): DataUpdate =>
  ({ ...c, path: evalUpdatePath(c), value: evalUpdateValue(c) })

const initDataUpdates = (schema: Schema): Update[] =>
  flatten(schema.map(initFieldUpdates(schema, '')))

const initFieldUpdates = (schema: Schema, parentPath: string = '') => (f: Field): Update[] =>
  use(pre(parentPath, f.name))(
    when<string, Update[]>(
      p => [
        createDataUpdate(p, { value: '' }, UpdateType.ui),
        createRuleUpdate(createInputRule(p))
      ],
      [() => isListField(f), p => [createDataUpdate(p, { listOp: ListOp.clear })]],
      [() => isOptionField(f, schema), p => [
        createDataUpdate(p, { listOp: ListOp.clear }),
        createDataUpdate(p, { value: getField(schema, f.type, 'options'), listOp: ListOp.add }, UpdateType.ui),
        createRuleUpdate(createInputRule(p))
      ]],
      [() => isObjectField(f), p => flatten(f['fields'].map(initFieldUpdates(schema, p)))]
    )
  )

export const ruleToUpdates = cases<RuleContext, Update[]>(c => c.action, [], {
  message: c => [
    createEffectUpdate(() => alert(c.target))
  ],
  sync: c => [
    createEffectUpdate(() => sync(c.context))
  ],
  initData: c => [
    ...initDataUpdates(c.context.schema),
    ...dataToUpdates(c.context.data),
    createEffectUpdate(() => loadData(c.context), true),
    ...rulesToUpdates(c.context.rules)
  ],
  input: c => [
    createDataUpdate(c.target, { value: c.eventData.target.value }, UpdateType.data)
  ],
  assign: c => [
    createDataUpdate(c.target, { calcValue: c.params?.exp && evalBinaryExp(c.params.exp) })
  ],
  add: c => [
    createDataUpdate(c.target, {
      listOp: ListOp.add,
      calcValue: (data: Data) => !c.params?.item ? [] : [{
        ...get(c.params.item, data),
        _uid: nextUID(get(c.target, data))
      }]
    })
  ],
  remove: c => [
    createDataUpdate(c.target, { listOp: ListOp.delete, value: findAttr('_uid', c.eventData.srcElement) })
  ],
  sort: c => [
    createDataUpdate(c.target, {
      listOp: ListOp.sort,
      calcValue: (data: Data) => sortList(c.target, c.params?.field, c.params?.order, data)
    })
  ],
  filter: c => c.params?.predicate && [
    createDataUpdate(c.target, {
      listOp: ListOp.filter,
      calcValue: (data: Data) => filterList(c.target, c.params?.predicate, data)
    })
  ]
})

export const rulesToUpdates = (rules: Rule[], parent?: UIElement, target?: string) =>
  fmap(r => !isWindowRule(r) && (!target || r.output.target == target), r => createRuleUpdate(r, undefined, parent), rules)

export const createDataUpdate = (path: string, payload: object, type: UpdateType = UpdateType.data_ui): DataUpdate =>
  ({ type, path, value: '', listOp: 0, calcValue: () => '', ...payload })

export const createRuleUpdate = (rule: Rule, value?: Value, parent?: UIElement): RuleUpdate =>
  ({ type: UpdateType.rule, value , ...rule, parent })

export const createEffectUpdate = (effect: Effect, wait: boolean = false): EffectUpdate =>
  ({ type: UpdateType.effect, value: '', effect, wait })

export const createUpdateContext = (context: Context, du: DataUpdate): UpdateContext =>
  ({ ...context, ...du })

export const createDataContext = (path: string, value: Value, listOp: ListOp, data: Data): DataContext =>
  ({ ...createDataUpdate(path, { value, listOp }), data })

export const sync = ({ data }: Context) =>
  Promise.all(Object.entries(data).map(([doc, value]) => isArray(value) && post({ doc, body: { [doc]: value } })))
    .then(r => alert('saved'))

export const loadData = (c: Context) =>
  use(fmap(x => x.type === 'list', x => x.name, c.schema))(docs =>
    Promise.all(docs.map(doc => api({ doc })))
      .then(r => update(c, r.map((l, i) => createDataUpdate(docs[i], { value: l, listOp: ListOp.add }))))
  )

export const dataToUpdates = (data: Data): Update[] =>
  Object.entries(data).map(([k, v]) => createDataUpdate(k, { value: v }, UpdateType.ui))