import { Schema, Field, ObjectField } from 'type/schema'
import { Template } from 'type/context'
import { isListField, isTemplateField, isObjectField, isDefined } from 'type/guard'
import { use, when, flatten, tap } from 'utils'
import { pre } from 'utils/path'
import { pathToElements } from './web'

export const schemaToTemplates = (schema: Schema, parentPath: string): Template[] =>
  flatten(schema.map(fieldToTemplates(schema, parentPath)))

const fieldToTemplates = (schema: Schema, parentPath: string) => when<Field, Template[]>(
    [],
    [f => isTemplateField(f, schema), listFieldToTemplate(parentPath)],
    [isObjectField, f => f['fields'].map(fieldToTemplates(schema, pre(parentPath, f.name)))]
  )

const listFieldToTemplate = (parentPath: string) => (field: Field): Template[] =>
  use(pre(parentPath, field.name))(listPath =>
    use(pathToElements(listPath + '.0'))(elements =>
      elements.length > 0
        ? [{ listPath, type: isListField(field) ? field.itemType : field.type, element: elements[0] }]
        : []
    )
  )
