import { curry, pipe, lensPath, view, set as _set, over, remove, sortWith, descend, ascend, prop } from 'ramda'
import { BoolMap, LensPath } from 'type/lang'
import { Value, Predicate } from 'type/rule'
import { ListOp } from 'type/enum'
import { Data } from 'type/context'
import { use, tap, cases, lastSplit2, getStringLiteral } from '.'
import { evalBinaryExp } from './expression'
import { DataContext, SetContext } from 'type/update';
import { createDataContext } from 'ui/update'
import { isArray } from 'type/guard'

export const pre = (s1: string, s2: string): string =>
  (s1 ? (s1 + '.') : '') + s2

export const pathToArray = (p: string): LensPath => p
  .replace(/\[/g, ".")
  .replace(/\]/g, "")
  .split(".")
  .map(x => isNaN(+x) ? x : +x)

const lens = pipe(
  pathToArray,
  lensPath
)

export const get = curry(
  <T extends Value>(p: string, o: object) => view<object, T>(lens(p))(o)
)

export const setValue = (c: DataContext): Data =>
  _set(lens(c.path))(c.value, c.data)

export const set = cases<DataContext, Data>(
  c => c.listOp,
  setValue,
  [ListOp.filter, c => c.data],
  [ListOp.add, c => isArray(c.value) ? setAll(...c.value.map((v, i) => use(lastSplit2('.')(c.path))(([listPath, idx]) => [listPath + '.' + (+idx + i), v, ListOp.add] as SetContext)))(c.data) : setValue(c)],
  [ListOp.delete, c => c.value ? over(lens(c.path), remove(+c.value, 1), c.data) : c.data]
)

export const setPath = (v: Value, l: ListOp, o: Data) => (p: string) => set(createDataContext(p, v, l, o))

export const setAll = (...vs: SetContext[]) => (obj: Data) =>
  vs.reduce((o, [k, v, l]) => set(createDataContext(k, v, l, o)), obj)

export const setWhen = <T>(boolMap: BoolMap<T>) => (obj: Data, value: T) =>
  use(Object.entries(boolMap).find(([k, v]) => v(value)))(
    r => r ? set(createDataContext(r[0], value, 0, obj)) : obj
  )

export const filterList = (path: string, predicate: Predicate | undefined, data: Data) =>
  predicate && use(get(path, data), getStringLiteral(predicate.op2) ?? get(predicate.op2, data))(
    (list, search) => list.filter(x => evalBinaryExp(predicate)(data, x[predicate.op1], search) === 'true')
  )

export const sortList = (path: string, field: string, order: string, data: Data) =>
  use(get(path, data))(
    list => sortWith([(order === 'descending' ? descend : ascend)(prop(field))], list)
  )
