import { contains } from 'ramda'
import { Expression } from 'type/rule'
import { Data } from 'type/context'
import { isUndefined } from 'type/guard';
import { get } from './path'
import { tap } from 'utils';

export const evalBinaryExp = (p: Expression) => (data: Data, x1?: string, y1?: string): string => {
  // const x = (isString(op1) ? op1 : (p.op1 ? get(p.op1)(op1) : '')) as string;
  // const y = (isString(op1) ? op2 : (p.op2 ? get(p.op2)(op1) : '')) as string;

  const x = isUndefined(x1) ? (p.op1 ? get(p.op1)(data) : '') : x1
  const y = isUndefined(y1) ? (p.op2 ? get(p.op2)(data) : '') : y1

  let z;
  let op = p.operator;
  let not = false;
  if (op.slice(0, 1) === '!') {
    not = true;
    op = op.slice(1);
  }

  if (op === '~') z = contains(y, x);
  else if (op === '=') z = x == y;
  else {
    const a = +x;
    const b = +y;
    if (op === '+') z = a + b;
    else if (op === '-') z = a - b;
    else if (op === '*') z = a * b;
    else if (op === '/') z = a / b;
    else if (op === '>') z = a > b;
    else if (op === '<') z = a < b;
    else if (op === '>=') z = a >= b;
    else if (op === '<=') z = a <= b;
  }

  if (not) z = !z;

  return z.toString();
}

