import { find } from 'ramda'
import { UIElement } from 'type/ui'
import { Value, Rule } from 'type/rule'
import { Context, Template } from 'type/context'
import { ListOp } from 'type/enum'
import { RuleUpdate, UpdateContext } from 'type/update'
import { NestedList } from 'type/lang'
import { isUID, isNumber } from 'type/guard'
import { use, run, seq, returnIfEmpty, cases, tap, lastSplit2, fget, flatten } from 'utils'
import { pathToElements, cloneElement, insertToList, clearList as _clearList, deleteFromList as _deleteFromList } from './web'
import { createDataUpdate, rulesToUpdates } from './update'
import { updateUIElement } from './element'

// steps

const get_listPath_idx = (path: string) =>
  use(lastSplit2('.')(path))(a => isNaN(+a[1]) ? [path, 0] : a)

const get_template = (listPath: string, templates: Template[]) =>
  fget(t => t.listPath === listPath, t => t.element, templates)

const make_clone = (template: UIElement, value: Value) =>
  (value as []).map(v => cloneElement(template))

const build_cloneUpdates = (value: Value, _ctx: UpdateContext) =>
  flatten((value as []).map((v, i) =>
    updateUIElement({ ..._ctx, path: '', listOp: 0, value: v, parent: _ctx['clone'][i] })
  ))

const _insert_clone_to_list = (listPath: string, clone: UIElement[], idx: string) =>
  pathToElements(listPath).forEach(l => clone && insertToList(clone, l, +idx))

const get_ruleUpdates = (rules: Rule[], clone: UIElement[], listPath: string, cloneUpdates: RuleUpdate[]) =>
  cloneUpdates.concat(
    flatten(
      clone.map(c => rulesToUpdates(rules, c, listPath))
    )
  )

const init_path = (path: string) => path + '.0'
  
// actions

const addToList = seq<UpdateContext, RuleUpdate[]>(
  //tap,
  get_listPath_idx,
  //tap,
  get_template,
  //tap,
  returnIfEmpty('template', []),
  make_clone,
  //tap,
  build_cloneUpdates,
  //tap,
  _insert_clone_to_list,
  //tap,
  get_ruleUpdates,
)

const clearList = ({path}) =>
  pathToElements(path).forEach(_clearList)

const deleteFromList = ({path, value}) =>
  pathToElements(path).forEach(e => _deleteFromList(e, value))

const filterList = seq<UpdateContext, RuleUpdate[]>(
  clearList,
  init_path,
  addToList
)

export const updateListUIElement = cases<UpdateContext, RuleUpdate[]>(
  c => c.listOp, [],
  [ListOp.add, addToList],
  [ListOp.clear, clearList],
  [ListOp.delete, deleteFromList],
  [ListOp.filter, filterList],
  [ListOp.sort, filterList],
)
